.catalog-making {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border:1px solid #ececec;
  margin: 0 0 58px;
}
.catalog-making__headline-container {
  margin: 0;
  padding: 0;
  position: relative;
}
.catalog-making__headline-container svg {
  display: block;
  position: absolute;
  top: 30px;
  left: 34px;
}
.catalog-making__headline-container path{
  fill: #63d1bb;
}
.catalog-making__headline {
  width: 170px;
  margin: 0px;
  padding: 25px 0 25px 90px;
  font-family: "OpenSans",sans-serif;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  text-align: left;
}
.catalog-making__video {
  position: relative;
  margin: 0;
  padding: 0;
  padding-bottom: 20px;
}
.catalog-making__video-button{
  display: block;
  margin: 0 auto;
  border: none;
  background-color: transparent;
  outline: none;
  position: absolute;
  top: 34px;
  left: 74px;
}
.catalog-making__video svg {
  position: absolute;
  top: 35px;
  left:80px;
}
.catalog-making__description {
  padding: 0 37px 0 33px;
  margin: 0 0 25px;
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
  color: #000000;
  text-align: left;
}
.catalog-making__buy {
  display: block;
  margin: 0px 34px 32px;
  padding: 16px 0;
  text-decoration: none;
  font-weight: bold;
  font-size: 17px;
  line-height: 16px;
  text-transform: uppercase;
  font-weight: bold;
  color: #444444;
  text-align: center;
  border: 2px solid #444444;
  &:hover{
    background: #444444;
    color: white;
  }
  &:active{
    color: #8d8d8d;
  }
}
@media (min-width: @tablet-width) {
  .catalog-making{
    margin: 0 0 65px;
  }
  .catalog-making__headline-container svg {
    top: 27px;
    left: 41px;
  }
  .catalog-making__video-button{
    top: 185px;
    left: 275px;
  }
  .catalog-making__video {
    order: -1;
    padding: 0;
  }
  .catalog-making__headline {
    font-size: 20px;
    width: auto;
    padding: 32px 0 32px 105px;
  }
  .catalog-making__description {
    padding: 0 45px 10px 40px;
    font-weight: 400;
    font-size: 15px;
    line-height: 30px;
  }
  .catalog-making__buy {
    margin: 0 40px 45px;
  }
}
@media (min-width: @desktop-width) {
  .catalog-making {
    position: relative;
    flex-direction: row;
    flex-wrap: wrap;
    min-height: 453px;
    justify-content: flex-end;
  }
  .catalog-making__headline-container {
    padding-left: 710px
  }
  .catalog-making__headline {
    padding-top: 100px;
  }
  .catalog-making__headline-container svg {
    padding-left: 800px;
    left: 0px;
    top: 47px;
  }
  .catalog-making__description {
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    width: 235px;
    padding-left: 620px;
    margin-right: 30px;
  }
  .catalog-making__video {
    position: absolute;
    left: 0px;
  }
  .catalog-making__buy {
    width: 230px;
    margin-right: 60px;
  }
}
